button {
  background-color: red;
}

h1, h2 {
  margin: 2rem auto;
  font-weight: 400;
}

h1, h2, p, .container {
  text-align: center;
  margin: 1rem auto;
}

.container div:first-child {
  vertical-align: middle;
  padding-top: 3rem;
  position: relative;
}

.focusTooltip {
  visibility: hidden;
  width: 300px;
  color: #fff;
  text-align: center;
  z-index: 1;
  background-color: #000;
  border-radius: 6px;
  margin: auto;
  padding: 5px 0;
  position: absolute;
  top: -10px;
  left: 0;
  right: 0;
}

.focusTooltip:after {
  content: "";
  border: 5px solid #0000;
  border-top-color: #000;
  margin-left: -5px;
  position: absolute;
  top: 100%;
  left: 50%;
}

.container:hover .focusTooltip {
  visibility: visible;
}

